import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import CategoryType from './CategoryType'
import StringEntity from './StringEntity'
import GlobalSettings from '@/GlobalSettings'
import IKUtils from 'innerken-js-utils'
import store from '../store'
import AttributeGroup from './AttributeGroup'

const i = Boolean(GlobalSettings.isShowOnTable === '0')

const colorList = ['#FFCDD2', '#F8BBD0', '#E1BEE7',
  '#D1C4E9', '#C5CAE9', '#BBDEFB',
  '#B3E5FC', '#B2EBF2', '#B2DFDB',
  '#C8E6C9', '#DCEDC8', '#F0F4C3',
  '#FFF9C4', '#FFECB3', '#FFE0B2',
  '#FFCCBC', '#D7CCC8', '#CFD8DC']

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    form: false,
    table: false,
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: false,
    },
  },
  dcImage: {
    type: IKDataEntity.Types.Image,
    formConfig: {
      required: false,
      type: {
        root: () => store.getters.rootUrl + GlobalSettings.dcImageRoot,
      },
    },
    displayName: 'image',
    header: i,
  },
  attributeGroupId: {
    displayName: 'attributeGroupNames',
    type: IKDataEntity.Types.Option,
    tableConfig: {
      width: '100px',
    },
    formConfig: {
      required: false,
      type: {
        selectItems: AttributeGroup.getList,
        multiple: true,
      },
    },
  },
  _langsname: {
    displayName: 'KategorieName',
    form: false,
  },
  langs: {
    displayName: 'labelName',
    type: IKDataEntity.Types.Group,
    formConfig: {
      groupName: 'labelName',
      default: GlobalSettings.supportLang,
      childLabelKey: 'lang',
      type: {},
    },
    tableConfig: {
      displayCondition: (item) => {
        return item.lang.toUpperCase() === GlobalSettings.getLang().toUpperCase()
      },
      displayChild: (key) => {
        console.log(key)
        return key === 'name'
      },
    },
    childKey: ['name', 'desc'],
    labelKey: 'lang',
    children: [
      StringEntity, StringEntity, StringEntity,
    ],
    header: false,
  },
  dishesCategoryTypeId: {
    displayName: 'warengruppe',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: CategoryType.getList,
      },
    },
  },

  printOrder: {
    type: IKDataEntity.Types.Integer,
    required: false,
    formConfig: {
      default: 50,
      requiredNew: false,
      requiredEdit: false,
      rule: [ // ^-[1-9]\d*$
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die ganze Zahl ein',
      ],
    },
  },
  categorySort: {
    type: IKDataEntity.Types.Integer,
    displayName: 'sectionSort',
    formConfig: {
      required: true,
    },
  },

}

const config = {

  add: function (item) {
    item.appliedAttributeGroup = item.attributeGroupId.join(',')
    item.catTypeId = item.dishesCategoryTypeId
    return hillo.post('Category.php?op=add', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  edit: function (item) {
    item.appliedAttributeGroup = item.attributeGroupId.join(',')
    item.catTypeId = item.dishesCategoryTypeId

    console.log('label-manage: item()', item)
    return hillo.postWithUploadFile('Category.php?op=update', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Category.php?op=delete', { id: id })
  },
  load: async function (filter) {
    const response = (await hillo.get('Category.php', filter)).content
    console.log(response, 'cate response')
    return response
  },
  randomColor: async function (list) {
    IKUtils.showLoading(true)
    for (const item of list) {
      item.appliedAttributeGroup = item.attributeGroupId.join(',')
      item.catTypeId = item.dishesCategoryTypeId

      item.color = colorList[Math.round(Math.random() * 18)]

      await hillo.postWithUploadFile('Category.php?op=update', {
        ...item,
        langs: JSON.stringify(item.langs),
      }, {
        showLoading: false,
      })
    }
    IKUtils.toast('OK')
  },

  removeAllColor: async function (list) {
    IKUtils.showLoading(true)
    for (const item of list) {
      item.appliedAttributeGroup = item.attributeGroupId.join(',')
      item.catTypeId = item.dishesCategoryTypeId
      item.color = '#FFFFFF'
      await hillo.postWithUploadFile('Category.php?op=update', {
        ...item,
        langs: JSON.stringify(item.langs),
      }, {
        showLoading: false,
      })
    }
    IKUtils.toast('OK')
  },

  removeCategoryImage: async function (categoryId) {
    await hillo.post('Category.php?op=removeCategoryImage', {
      ...categoryId,
    }, {
      showLoading: true,
    })
  },

}

// export default IKDataEntity.ModelFactory(entity, config)
export const res = IKDataEntity.ModelFactory(entity, config)
console.log('Category', res)
export default res
